.container {
	display: flex;
	flex-direction: column;
	padding: 10px;
	background-color: white;
	border-radius: 8px;
}
/* FIRST PART */
.firstPart {
	display: flex;
	flex-direction: column;
	margin: 7px 5px;
}
label, .select-tip {
    margin: 5px 0;
    font-size: 16px;
    font-weight: 700;
    color: #76807F;
}
.select-tip {
    margin-top: 10px;
}
.cash-icon,
.input-people {
	padding: 8px;
	text-align: right;
    font-weight: 700;
    font-size: 18px;
	background-color: #F3F8FB;
	background-position: left;
    background-position-x: 10px;
	background-repeat: no-repeat;
    color: #265253;
	border: none;
	border-radius: 5px;
    outline: none; 
    transition: border-color 0.3s; 
}
.cash-icon:focus,
.input-people:focus {
    border: 2px solid #26C2AD; 
}
.cash-icon{
	background-image: url('../images/icon-dollar.svg');   
}
.input-people {
	background-image: url('../images/icon-person.svg');
}
.tips {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
    margin-bottom: 7px;
}

.tip,
.custom {
    flex-basis: 48%;
}
.tip {
    margin: 8px 0 0;
    padding: 8px 3px;
	text-align: center;
	font-size: 20px;
	font-weight: 700;
    cursor: pointer;
	background-color: hsl(183, 100%, 15%);
	color: white;
    border-radius: 5px;
}
.custom-tip {
    width: 100%;
	height: 80%;
    margin: 8px 5px 8px 0;
	text-align: right;
    font-size: 16px;
	font-weight: 700;
	background-color: #F3F8FB;
    color:#265253;
	border: none;
    border-radius: 8px;
    outline: none; 
    transition: border-color 0.3s; 
} 
.custom-tip:focus {
    border: 2px solid #26C2AD; 
}
.custom-tip::placeholder {
    margin-left: 5px;
    font-size: 18px;
    color:#265253;
    
}
.labelgroup {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}
.error {
    display: none;
    margin: 7px 0;
    font-size: 14px;
    color: red;
}
/* SECOND PART  */

.partTwo {
    padding: 10px;
    margin: 15px 5px;
    background-color: hsl(183, 100%, 15%);
    border-radius: 8px;
}
.top,.bottom {
    display: flex;
    justify-content: space-between;
    margin: 15px 5px 20px 5px;
    color: white;

} 
.text {
    font-size: 14px;
}
.person {
    font-size: 12px;
    color:hsl(184, 14%, 56%);
}
.amount {
    font-size: 22px;
    font-weight: 700;
    color:#21C5AD;
}
.btn-reset {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}
.reset {
    width: 90%;
    padding: 10px;
    font-weight: 600;
    font-size: 16px;
    background-color: #26C2AD;
    color: hsl(183, 100%, 15%);
    border: 0;
    border-radius: 5px;
}

@media (min-width: 768px) {
    body {
        align-items: center;
    }
    .container {
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: nowrap;
    }
    .firstPart,.partTwo {
        flex-basis: 45%;
        margin: 10px 15px
        
    }
    input {
        font-size: 24px;
    }
    .select-tip, .labelgroup {
        margin-top: 20px;
    }
    .select-tip,label {
        font-size: 18px;
    }

    .tip, .custom {
        flex-basis: 30%;
        
    }
    .top,.bottom {
        margin: 25px 15px;
    }
    .text {
        font-size: 18px;
    }
    .amount {
        font-size: 32px;
    }
    .btn-reset {
        margin-top: 120px;

    }
    
}